<template>
  <div class="reset-pwd-box">
    <p v-show="!resetSent" class="reset-password" @click="waResetPassword">Forgot Password</p>
    <p v-show="resetSent" class="reset-password-tip">{{ resetFinishTip }}</p>
  </div>
</template>

<script>
import {resetText} from "../messages/label-text"
import {dispatchCustomEvent} from "../../utils/event-util";
import md5 from "js-md5";

export default {
  name: "ForgotPwd",
  data() {
    return {
      ...resetText,
      resetSent: false,
    }
  },
  methods: {
    waResetPassword() {
      if (confirm(this.resetConfirmTip)) {
        const random = Math.random().toString(36).substring(7);
        const user_whatsapp_id = window.Store2.Conn2.wid._serialized;
        const chat_model = window.Store2.Chat.get(user_whatsapp_id);
        chat_model.sendMessage(`Your new WhatsApp Web password is:${random}`);
        dispatchCustomEvent('forgetLockPasswd', {passwd: md5(random)})
        localStorage.setItem('lockScreenPassword', md5(random));
        this.resetSent = true;
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.reset-pwd-box {
  p {
    color: red;
    opacity: 0.55;
    cursor: pointer;
    display: inline-block;

    &.reset-password-tip {
      font-size: small;
      margin-top: 10px;
      color: green;
      line-height: 1.2;
      pacity: 0.55;
      cursor: pointer;
      display: inline-block;
    }
  }
}

</style>
