import Vue from 'vue'
import App from './App.vue'


function createContainer(parentNode, id) {
    const container = document.createElement('div')
    container.setAttribute('id', id)
    parentNode.appendChild(container)
    return container
}

let timer = null;

function addLockWrapper() {
    const contentDiv = document.querySelector('.app-wrapper-web > div.two')
    if (!contentDiv) {
        timer = setTimeout(addLockWrapper, 1e3);
        return;
    }

    if (!contentDiv.classList.contains('wa-lock')) {
        contentDiv.classList.add('wa-lock');
        const wrapper = document.querySelector('.app-wrapper-web')
        console.log(wrapper);
        createContainer(wrapper, 'wa-lock-wrapper')
        /* eslint-disable no-new */
        new Vue({
            render: h => h(App)
        }).$mount("#wa-lock-wrapper");
    }

    clearTimeout(timer);
}

export default addLockWrapper;
