<template>
  <div class="avatar-box">
    <img class="avatar"
         :src="avatar"/>
  </div>
</template>

<script>
export default {
  name: "Avatar",
  data() {
    return {
      timer: 0,
      times: 0,
      avatar: ""
    }
  },
  mounted() {
    this.timer = setTimeout(this.setAvatar, 350);
  },
  methods: {
    setAvatar() {
      const host = localStorage.getItem('chromeExtensionHost');
      const h = window.Store2.Contact.where({isMe: !0});
      if (h && h.length > 0) {
        this.avatar = h[0].__x_profilePicThumb?.__x_img;
      }
      this.times++;
      console.log(this.avatar);
      if (!this.avatar) {
        if (this.times < 3) {
          console.log("loop:", this.times)
          setTimeout(this.setAvatar, 350);
          return false;
        }
        clearTimeout(this.timer);
        this.avatar = host + "images/avatar.svg";
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.avatar-box {
  .avatar {
    border: 1px solid rgb(74, 223, 130);
    display: block;
    margin: 0 auto;
    width: 60px;
    border-radius: 50%;
  }
}

</style>
