import addLockWrapper from "./pages/main";

import {dispatchCustomEvent, listenEvent} from "../utils/event-util";
import {getBySelector} from "../utils/inject-func";
import {loadModule} from "./wapi";
import {importGlobalProperty} from "./globalProperty";

importGlobalProperty();

function checkAppLoadFullAndLoadModules() {
    if (getBySelector("div.app") ||
        getBySelector("div.two") ||
        getBySelector("div.three") ||
        getBySelector("div.pane-side")) {
        loadModule();
        clearTimeout(globalPropery.timerLoadModules);
        /**
         * wapi  
         */
        dispatchCustomEvent("startLockVerify", {})
        return true;
    }
    clearTimeout(globalPropery.timerLoadModules);
    globalPropery.timerLoadModules = setTimeout(checkAppLoadFullAndLoadModules, 1e3);
}

globalPropery.timerLoadModules = setTimeout(checkAppLoadFullAndLoadModules, 1e3);

/**
 * 
 */
listenEvent('startLockScreen', (data) => {
    localStorage.setItem('lockScreenPassword', data.lockPassword);
    addLockWrapper()
});

listenEvent('chromeExtensionHost', (data) => {
    console.log(data);
    localStorage.setItem('chromeExtensionHost', data.host);
});