<template>
  <div class="wa-lock-wrapper" v-show="showLock">
    <div class="wa-lock-inner" :class="{shake: passwordEmpty}">
      <Avatar></Avatar>
      <div class="input-holder">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 448" height="20">
          <path fill="red" opacity="0.55"
                d="M352 149.333h-21.333v-42.667C330.667 47.787 282.88 0 224 0S117.333 47.787 117.333 106.667v42.667H96c-23.573 0-42.667 19.093-42.667 42.667v213.333C53.333 428.907 72.427 448 96 448h256c23.573 0 42.667-19.093 42.667-42.667V192c0-23.573-19.094-42.667-42.667-42.667zm-128 192c-23.573 0-42.667-19.093-42.667-42.667C181.333 275.093 200.427 256 224 256s42.667 19.093 42.667 42.667c0 23.573-19.094 42.666-42.667 42.666zm66.133-192H157.867v-42.667c0-36.48 29.653-66.133 66.133-66.133s66.133 29.653 66.133 66.133v42.667z"></path>
        </svg>
        <input type="text" placeholder="**********" v-model="password" class="pw" @keyup.enter="unlock">
        <button @click="unlock">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
            <path fill="#FFF" d="M20 11H7.8l5.6-5.6L12 4l-8 8 8 8 1.4-1.4L7.8 13H20v-2z"></path>
          </svg>
        </button>
      </div>
      <br><br>
      <ForgotPwd></ForgotPwd>
    </div>

  </div>
</template>

<script>
import md5 from 'js-md5'
import ForgotPwd from "../components/ForgotPwd";
import Avatar from "../components/Avatar";


export default {
  name: "LockScreenWrapper",
  components: {Avatar, ForgotPwd},
  data() {
    return {
      avatar: "",
      password: "",
      showLock: true,
      passwordEmpty: false,
      resetSent: false
    }
  },
  methods: {
    unlock() {
      const lockPassword = localStorage.getItem("lockScreenPassword");
      if (this.password && lockPassword && md5(this.password) === lockPassword) {
        const contentDiv = document.querySelector('.app-wrapper-web > div.two');
        if (contentDiv) {
          contentDiv.classList.remove('wa-lock');
        }
        this.showLock = false;
      }

      this.passwordEmpty = true;
      setTimeout(() => {
        this.passwordEmpty = false;
      }, 1000);
    }
  }
}
</script>

